<template>
  <div class="page">
    <div class='head'>
      <div class='head-title'>LOGO维护</div>
    </div>
    <div style="padding:15vh 50px 50px 30%">
      <el-upload :on-change='onChange'
        :on-success='onSuccess'
        :on-error='onError'
        :show-file-list='false'
        :auto-upload="false"
        ref='uploadLogo'
        :action="uploadFileUrl"
        :data='{projectName: "phy"}'>
        <template>
          <div class="el-upload-list el-upload-list--picture-card"
            style="display:block;margin-bottom:20px;">
            <div class="el-upload-list__item is-success"
              style="display:block;margin:0;width:240px;height:240px"
              v-if="hasImgUrl"
              @click.stop>
              <img :src="selectionFileUrl || logoUrl"
                class="el-upload-list__item-thumbnail"
                alt="LOGO">
              <a class="el-upload-list__item-name">
                <i class="el-icon-document"></i>
              </a>
              <label class="el-upload-list__item-status-label">
                <i class="el-icon-upload-success el-icon-check"></i>
              </label>
              <i class="el-icon-close"></i>
              <span class="el-upload-list__item-actions">
                <span class="el-upload-list__item-delete"
                  @click.stop="delLogo">
                  <i class="el-icon-delete"
                    style="font-size:28px"></i>
                </span>
              </span>
            </div>
            <div style="height:240px;width:240px;border:1px solid #c0ccda;border-radius:6px"
              class="flex-center flex-v-center"
              v-else>
              <i class="el-icon-plus"
                style="font-size:50px;color:#8c939d"></i>
            </div>
          </div>
          <el-button class="btn-red"
            style="width:110px">选取图片</el-button>
          <el-button class="btn-info"
            style="margin-left:20px;width:110px"
            @click.stop='saveLogo'
            :loading="submitLoading">保存</el-button>
        </template>
        <p slot="tip"
          style="padding-top:10px;font-size:12px;color:#606266">
          要求：jpg/png格式，大小不超过500kb
        </p>
      </el-upload>
    </div>
  </div>
</template>

<script>
import { concatUploadFileUrl } from '@/lib/utils'
export default {
  name: 'LogoManage',
  data() {
    return {
      logoUrl: '',
      selectionFileUrl: '',
      isUploadSuccess: false,
      submitLoading: false
    }
  },
  created() {
    this.getLoginOrg()
  },

  methods: {
    // 获取机构信息（LOGO）
    getLoginOrg() {
      this.$post('/sys/org/getLoginOrg')
        .then(({ data }) => {
          this.logoUrl = data.ologo
        })
        .catch(err => {
          err && this.$message.error(err)
        })
    },

    // 保存LOGO
    saveLogo() {
      if (!this.selectionFileUrl) return this.$message.warning('请选取您要上传的图片！')
      this.submitLoading = true
      this.$refs.uploadLogo.submit()
    },

    // 删除当前图片
    delLogo() {
      this.logoUrl = ''
      this.selectionFileUrl = ''
    },

    // 上传成功
    onSuccess({ data }) {
      this.$post('/sys/org/addLogoUrl', {
        orgId: this.$store.getters.o_userinfo.orgGuid,
        ologo: concatUploadFileUrl(data.filePath)
      })
        .then(res => {
          this.submitLoading = false
          this.$message.success('保存LOGO成功')
        })
        .catch(err => {
          this.submitLoading = false
          this.$message.error(err)
        })
    },

    // 上传失败
    onError(err) {
      console.log(err)
      this.submitLoading = false
      this.$message.error('图片上传失败')
    },

    // 选取文件成功
    onChange({ raw }) {
      let { type, size } = raw
      if (size > 500000) return this.$message.warning('LOGO图片大小不能超过500kb')
      if (['image/jpeg', 'image/jpg', 'image/png'].indexOf(type) === -1) {
        return this.$message.warning('LOGO图片格式只能是jpg/png')
      }
      let reader = new FileReader()
      reader.readAsDataURL(raw)
      reader.onload = e => {
        this.selectionFileUrl = reader.result
      }
    }
  },

  computed: {
    uploadFileUrl() {
      return concatUploadFileUrl('/common/file/uploadFile')
    },
    hasImgUrl() {
      return this.logoUrl || this.selectionFileUrl
    }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  line-height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
}
</style>


